import React from 'react';
import './App.css';
import CodeBox from './CodeBox';
import LanguagesList from "./LanguagesList";
import githubLogo from './images/github-logo.png';
import npmLogo from './images/npm-logo.png';
import {ToastContainer} from 'react-toastify';

function App() {
  return (
    <div className="main">
        Type a programming language and press enter 🤓
        <LanguagesList/>
        <hr/>
        <strong>Install it now</strong>
        <CodeBox>npm i github-lang-colors</CodeBox>
        <div className="logos">
        <a href="https://github.com/lluiscamino/github-lang-colors" target="_blank" rel="noopener noreferrer">
          <img src={githubLogo} alt="GitHub logo"/>
        </a>
        <a href="https://www.npmjs.com/package/github-lang-colors" target="_blank" rel="noopener noreferrer">
          <img src={npmLogo} alt="NPM logo"/>
        </a>
        </div>
        <footer>Ⓒ {new Date().getFullYear()} Lluís Camino</footer>
        <ToastContainer />
    </div>
  );
}

export default App;
