import React from 'react';
import './LanguagesList.css'
import getLangColor from 'github-lang-colors';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class LanguagesList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {languages: []};
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e) {
        this.setState({languages: [...this.state.languages, this.textInput.value]});
        if (getLangColor(this.textInput.value) instanceof Error) {
            toast.warn('⚠️ That doesn\'t seem to be a programming language', {
                position: 'top-right',
                autoClose: 1800,
                hideProgressBar: false,
                closeOnClick: true,
            });
        }
        this.textInput.value = '';
        e.preventDefault();
        e.stopPropagation();
    }

    render() {
        const langs = this.state.languages.map((lang, index) => {
            return (<div key={index} style={{
                backgroundColor: getLangColor(lang)
            }}>{lang}</div>);
        });
        return (
            <>
                <form onSubmit={this.handleSubmit}>
                    <input type="text" ref={(textInput) => this.textInput = textInput} placeholder="i.e JavaScript"/>
                </form>
                <div className="languages">
                    {langs}
                </div>
            </>
        );
    }
}
export default LanguagesList;